import React from "react";
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';

import Logo from "../../img/logo.png";
import './Header.css';

class Header extends React.Component {

    componentDidMount() {
        window.scrollTo(0, 0)
    }

    render() {
        return (
            <Navbar className="headerNav" expand="lg">
                <Navbar.Brand className="brandLogo" href="#/">
                    <img className="logo" src={Logo} alt="logo" />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="menu-navbar" />
                <Navbar.Collapse id="menu-navbar">
                    <Nav className="menu">
                        <NavDropdown href="hotel" title=" " id="basic-nav-dropdown">
                            <NavDropdown.Item href="#/hotel/standart"></NavDropdown.Item>
                            <NavDropdown.Item href="#/hotel/semilux"></NavDropdown.Item>
                            <NavDropdown.Item href="#/hotel/lux"></NavDropdown.Item>
                        </NavDropdown>
                        <NavDropdown title=" " id="basic-nav-dropdown">
                            <NavDropdown.Item href="#/house-rent/house-on-water">  </NavDropdown.Item>
                            <NavDropdown.Item href="#/house-rent/house-on-land">  </NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="#/banya"><span id="blink7">NEW!</span></Nav.Link>
                        <NavDropdown title="" id="basic-nav-dropdown">
                            <NavDropdown.Item href="#/fitnes"></NavDropdown.Item>
                            <NavDropdown.Item href="#/spa">SPA</NavDropdown.Item>
                            <NavDropdown.Item href="#/beaury365">365 STUDIO</NavDropdown.Item>
                            <NavDropdown.Item href="#/restoran"> Zafferano</NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="#/about"> </Nav.Link>
                        <Nav.Link href="#/contacts"></Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        );
    }
}

export default Header;
